import React, { useState, FC, FormEvent } from "react";

export const RoverSelector: FC<{ options: string[]; onSelect: (value: string) => void }> = ({
  onSelect,
  options
}) => {
  const [rover, setRover] = useState<string>("");

  const handleSelectChange = (value: string) => {
    return (event: FormEvent<HTMLOptionElement>) => {
      setRover(event.currentTarget.value);
      onSelect(value.toString());
    };
  };

  return (
    <div className='selector'>
      <select value={rover}>
        {options.map((item, i) => (
          <option value={item} onChange={handleSelectChange(item)}>
            {item}
          </option>
        ))}
        {/* 
        <option value='Opportunity'>Opportunity</option>
        <option value='Spirit'>Spirit</option> */}
      </select>
    </div>
  );
};

export default RoverSelector;
